.body {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  position: absolute;
  top: -96px;
  left: -30px;
  padding: 96px 0 0 0;
  max-width: 540px;
  max-height: 835px;
  width: 27vw;
  height: auto;
  color: var(--mainColor);
  border: 1px solid #CECFDB;
  border-radius: 0 0 4px 4px;
  background-color: transparent;
}
.figure {
  position: relative;
}
.image {
  max-height: 302px;
}
.caption {
  position: absolute;
  top: 20px;
  left: 20px;
  background-color: rgba(0, 0, 2, 0.16);
  backdrop-filter: blur(8px);
  border-radius: 4px;
  padding: 8px 12px;
}
.title {
  font-size: 24px;
  font-weight: 700;
  padding: 0 0 24px;
}
.footer {
  width: 100%;
}
.information {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 24px;
  height: 100%;
}
.statistic {
  display: flex;
  justify-content: space-between;
  border-bottom: 2px solid var(--lightBlue);
  width: 100%;
  padding-bottom: 2vw;
}
.view {
  display: flex;
  align-items: center;
}
.like {
  display: flex;
  align-items: center;
  margin: 0 1vw;
}
.read {
  font-size: 24px;
  display: flex;
  align-items: center;
  &:after {
    content: '';
    position: relative;
    top: 4px;
    border: solid var(--mainColor);
    border-width: 0 6px 6px 0;
    display: inline-block;
    padding: 7px;
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
  }
}
.nav {
  display: flex;
  justify-content: space-between;
  
  width: 100%;
  padding: 24px 0 0 0;
}
.prev {
  &:after {
    content: '';
    border-right: 2px solid var(--lightBlue);
    margin-left: 2vw;
  }
}
.next {
  display: flex;
  align-items: center;
  &:after {
    content: '';
    position: relative;
    top: 4px;
    border: solid var(--mainColor);
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 5px;
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);

  }
}
