@import-normalize;
@font-face {
  font-family: "Inter";
  src: url("./fonts/Inter-Bold.ttf") format("ttf");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  src: url("./fonts/Inter-Medium.ttf") format("ttf");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

/* Reset and base styles  */
* {
  padding: 0;
  margin: 0;
  border: none;
}

html {
  font-family: Inter, sans-serif;
  font-size: 10px;
  color: var(--mainColor);
  font-weight: 500;
  line-height: 1.2;
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
}

body {
  background-color: var(--black);
  font-size: 2rem;
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Links */

a, a:link, a:visited  {
  text-decoration: none;
  color: var(--mainColor);
}

a:hover  {
  text-decoration: none;
}

/* Common */

aside, nav, footer, header, section, main {
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  font-size: inherit;
  font-weight: inherit;
}

ul, ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

img, svg {
  max-width: 100%;
  height: auto;
}

address {
  font-style: normal;
}

/* Form */

input, textarea, button, select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

input::-ms-clear {
  display: none;
}

button, input[type="submit"] {
  display: inline-block;
  box-shadow: none;
  background-color: transparent;
  background: none;
  cursor: pointer;
}

input:focus, input:active,
button:focus, button:active {
  outline: none;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

label {
  cursor: pointer;
}

legend {
  display: block;
}

.background {
  width: 100%;
  height: 100%;
  min-height: 835px;
  background-size: cover;
  background-position: center;
  transition: background-image 0.5s ease;
}

.mask {
  background: rgb(255,255,255);
  background: linear-gradient(180deg,
          rgba(255,255,255,0) 0%,
          rgba(255,255,255,0) 0%,
          rgba(0,0,0,1) 100%);
  min-height: 835px;
  height: 100%;
}

.wrapper {
  max-width: 1920px;
  width: 88%;
  margin: 0 auto;
  position: relative;
}