.wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 0;
}
.logo {

}
.navigation {
  display: flex;
  align-items: center;
  color: var(--mainColor);
}
.list {
  display: flex;
  margin-right: 4rem;
}
.item {
  &:not(:last-child) {
    margin: 0 4rem 0;
  }
}
.link {
  font-size: 2rem;
  transition: .3s opacity ease;
  &:hover {
    opacity: 0.9;
    transition: .3s opacity ease;
  }
}
.control {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
}
.lang {
  position: relative;
  z-index: 2;
  margin-right: 3rem;
}