.body {
  background-color: var(--black);
}
.title {
  font-size: 44px;
  text-align: center;
  margin-bottom: 10rem;
}
.visualization {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 10rem;
}
.visualization__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  z-index: 2;
}
.visualization__img {
  max-width: 426px;
}
.visualization__img_turbine {
  position: relative;
  margin: 7rem 0;
  &:after {
    content: '';
    background-image: url("../../images/decor.png");
    position: absolute;
    top: 48%;
    left: 93%;
    display: block;
    max-width: 840px;
    width: 43vw;
    height: 1px;
  }
}
.visualization__title {
  font-size: 24px;
  font-weight: 700;
}
.slider {
  width: 40vw;
  height: 60px;
  max-width: 476px;
  margin: 0 auto 15rem;
  position: relative;
  display: flex;
  align-items: center;
}
.range {
  background-color: var(--range);
  border-radius: 8px;
  height: 8px;
  width: 100%;
}
.slider__img {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  transition: .3s all ease;
  img:hover {
    filter: brightness(0.8);
    transition: .3s all ease;
  }
}
.steps {
  display: flex;
  justify-content: space-between;
  padding-bottom: 15rem;
}
.step {
  display: flex;
  flex: 1 1 33%;
  flex-direction: column;
  align-items: center;
  padding: 0 15px;
}
.step__img {
  margin-bottom: 3rem;
}
.step__title {
  font-size: inherit;
}
