.position {
  display: flex;
  justify-content: flex-end;
  padding-bottom: 17.7rem;
}
.body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media (max-width:1440px ){
    width: 60vw;
  }
}
.text__block {
  display: flex;
  flex-direction: column;
  max-width: 726px;
}
.titles__block {
  padding: 2.4rem 0 4rem ;
  border-bottom: 2px solid var(--lightBlue);
}
.title {
  font-size: 36px;
  font-weight: 700;
}
.subtitle {
  font-size: 28px;
}
.tagline {
  font-size: 24px;
  font-weight: 700;
  padding: 4rem 0 12.2rem;
}
.social__media {
  display: flex;
  flex-direction: column;
}
.social__link {
  margin: 7px;
  cursor: pointer;
  transition: .3s all ease;
  img:hover {
    filter: brightness(0.8);
    transition: .3s all ease;
  }
}
.video {
  display: flex;
  align-items: center;
}
.video__play {
  transition: .3s all ease;
  margin-right: 24px;
  img:hover {
    filter: brightness(0.8);
    transition: .3s all ease;
  }
}
.video__info {
  .video__title {
    font-size: inherit;
  }
  .video__time {
    font-size: inherit;
  }
}
